import React from "react"
import AniLink from "gatsby-plugin-transition-link/AniLink"
import PropTypes from "prop-types"
import { LOGO_COLOR_DARK } from "./siteconfig"

const Logo = ({ title, color }) => (

    <div>
        <AniLink fade to="/" duration={.5}>
            <header css={{
                fontFamily: 'Open Sans',
                fontWeight: '300',
                fontSize: '18px',
                textTransform: 'uppercase',
                color: color,
                paddingTop: '30px',
                marginLeft: '8%',
                '@media(min-width:700px)': {
                    fontSize: '50px',
                    marginLeft: '8%'
                }
            }}>
                {title}
            </header>
        </AniLink>
    </div>

)

Logo.propTypes = {
    title: PropTypes.string,
}

Logo.defaultProps = {
    color: LOGO_COLOR_DARK
}


export default Logo


