import React from "react"
import SEO from "../components/seo"
import Logo from "../components/logo"
import Nav from "../components/navigation"
import IndexImage from "../images/index-image.jpg"
import { LOGO_COLOR_DARK } from "../components/siteconfig"

class IndexPage extends React.Component {
  constructor() {
    super()
    this.state = {
      isActive: "index"
    }
  }
  componentDidMount() {
    document.querySelector('html').style.overflowY = "hidden";
  }
  render() {
    return (
      <div style={{
        display: `flex`,
        backgroundImage: `url(${IndexImage})`,
        backgroundSize: `cover`,
        backgroundRepeat: `no-repeat`,
        backgroundPositionY: ` -400px`,
        width: `100%`,
        height: `2000px`
      }}>

        <SEO title="1240 Greene St" />
        <div css={{ width: '50%' }}>
          <Logo title="1240 Greene St" color={LOGO_COLOR_DARK} />
        </div>


        <div css={{ zIndex: `100`, marginLeft: `auto`, justifyContent: `flex-end` }}>
          <Nav isActive={this.state.isActive} />
        </div>


      </div>

    )
  }
}
